// Funcin para obtener el token del localStorage
function getToken() {
    return localStorage.getItem('token');
}

// Funcin para almacenar el token en el localStorage
function setToken(token) {
    localStorage.setItem('token', token);
}

// Funcin para eliminar el token del localStorage
function removeToken() {
    localStorage.removeItem('token');
}

// Funcin para verificar si hay un token almacenado
function hasToken() {
    return !!localStorage.getItem('token');
}

export { getToken, setToken, removeToken, hasToken };
